import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return{
            serviceId: this.$route.query.serviceId,
            user: this.$store.state.userInfo,
            info: null
        }
    },
    created(){
        this.pull()
    },
    updated() {
		const ele = document.getElementById('v_htmlCont').getElementsByTagName('img')
		const arr = [ ...ele ]
		arr.forEach(v => {
			v.style.cssText = "width: 100%;"
		})
	},
    methods: {
        pull(){
            this.$get(`PlatFormAPI/ServicePackItem/QueryServicePackAndDetailByPackId`,{
                ServicePackId: this.serviceId,
                DrId: this.user.Data.User.DoctorId
            }).then(o => {
                this.info = o
            })
        },
        shelf(v){
            this.$post(`PlatFormAPI/ServicePackItem/PackIsDisplay`,{
                ServicePackId: this.serviceId,
                Invalid: v,
            }).then(o => {
                this.pull()
                if(v == 0){
                    this.$toast('上架成功')
                }
                else{
                    this.$toast('下架成功')
                }
            })
        }
    }
}



